/* global transitions and animations */

.fade-enter,
.card-fade-enter {
  opacity: 0;
}

.fade-enter-active {
  -webkit-animation: openContent 1500ms ease-out forwards;
          animation: openContent 1500ms ease-out forwards;
}

@-webkit-keyframes openContent {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes openContent {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.card-fade-enter-active {
  -webkit-animation: openCard 1500ms ease-out forwards;
          animation: openCard 1500ms ease-out forwards;
}


@-webkit-keyframes openCard {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}


@keyframes openCard {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.flip-card-appear-active {
  -webkit-animation: flipCard 1000ms ease-out none;
          animation: flipCard 1000ms ease-out none;
}

@-webkit-keyframes flipCard {
  0% {
    -webkit-transform: rotate3d(.2, -1, -.2, -72deg);
            transform: rotate3d(.2, -1, -.2, -72deg);
  }
  100% {
    -webkit-transform: rotate3d(0, 0, 0, 0deg);
            transform: rotate3d(0, 0, 0, 0deg);;
  }
}

@keyframes flipCard {
  0% {
    -webkit-transform: rotate3d(.2, -1, -.2, -72deg);
            transform: rotate3d(.2, -1, -.2, -72deg);
  }
  100% {
    -webkit-transform: rotate3d(0, 0, 0, 0deg);
            transform: rotate3d(0, 0, 0, 0deg);;
  }
}

.switch-wrapper {
  position: relative;
  height: 100%;
}

.switch-wrapper > div {
  position: absolute;
  width: 100%;
  height: 100%;
}

/* global responsive formatting */
@media (min-width: 768px) {
  h1 {
    -webkit-margin-before: 0.67em;
            margin-block-start: 0.67em;
  }
}

